.solutions {
  position: relative;
  padding-top: 50px;
  padding-bottom: 370px;
  background-color: #ffffff;
  background: url("../img/vector/innertopbg.svg") left top -320px / 100% 1465px no-repeat;

  @media (max-width: $tabletwidth) {
    padding-top: 25px;
    padding-bottom: 100px;
    background-image: none;
  }

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    height: 370px;
    width: 100%;
    background: url("../img/vector/innerbottombg.svg") left top / 100% 370px, linear-gradient(220.86deg, #9851DF 4.06%, #54D6BF 90.75%);

    @media (max-width: $tabletwidth) {
      display: none;
    }
  }
}

.solutions__title {
  margin-bottom: 100px;

  @media (max-width: $tabletwidth) {
    display: none;
  }
}

.solutions__list {
  @include reset-list;
  display: flex;
  max-width: 1025px;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (max-width: 950px) {
    justify-content: center;
  }
}

.solutions__item {
  margin-bottom: 80px;

  @media (max-width: $tabletwidth) {
    margin-bottom: 25px;
  }
}
